import React from 'react'

function Wsus() {
  return (
    <section style={{
          padding: '4rem 2rem',
          backgroundColor: '#1a1a1a'
        }}>
          <h2 style={{
            fontSize: '2.5rem',
            textAlign: 'center',
            marginBottom: '3rem',
            color: '#c5fb45'
          }}>Why Choose KRUNAL&apos;S ACADEMY</h2>
          <div style={{
            display: 'grid',
            gridTemplateColumns: 'repeat(auto-fit, minmax(300px, 1fr))',
            gap: '2rem',
            maxWidth: '1200px',
            margin: '0 auto'
          }}>
            {[
              {
                title: 'Expert Instructors',
                desc: 'Learn from industry professionals with years of experience'
              },
              {
                title: 'Hands-on Training',
                desc: 'Practical sessions with real-world applications'
              },
              {
                title: 'Modern Facilities',
                desc: 'State-of-the-art equipment and learning environment'
              },
              {
                title: 'Career Support',
                desc: 'Job placement assistance after course completion'
              }
            ].map((item, index) => (
              <div key={index} style={{
                backgroundColor: '#2a2a2a',
                padding: '2rem',
                borderRadius: '8px',
                borderTop: '4px solid #c5fb45'
              }}>
                <h3 style={{
                  fontSize: '1.5rem',
                  marginBottom: '1rem',
                  color: '#c5fb45'
                }}>{item.title}</h3>
                <p>{item.desc}</p>
              </div>
            ))}
          </div>
        </section>
  )
}

export default Wsus