import React from 'react'

function Makeupservice() {
  return (
     <section style={{
          padding: '4rem 2rem',
          backgroundColor: '#0a0a0a'
        }}>
          <h2 style={{
            fontSize: '2.5rem',
            textAlign: 'center',
            marginBottom: '3rem',
            color: '#c5fb45'
          }}>Our Makeup Services</h2>
          <div style={{
            display: 'grid',
            gridTemplateColumns: 'repeat(auto-fit, minmax(250px, 1fr))',
            gap: '2rem',
            maxWidth: '1200px',
            margin: '0 auto'
          }}>
            {[
              {
                title: 'Face Makeup',
                desc: 'Master the art of flawless complexion and contouring'
              },
              {
                title: 'Eye Makeup',
                desc: 'Learn stunning eye looks from natural to dramatic'
              },
              {
                title: 'Eyebrow Makeup',
                desc: 'Perfect the techniques for beautiful brows'
              },
              {
                title: 'Haircut & Styling',
                desc: 'Transform hair with cutting-edge techniques'
              }
            ].map((item, index) => (
              <div key={index} style={{
                backgroundColor: '#1a1a1a',
                padding: '2rem',
                borderRadius: '8px',
                textAlign: 'center'
              }}>
                <div style={{
                  width: '80px',
                  height: '80px',
                  backgroundColor: '#c5fb45',
                  borderRadius: '50%',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  margin: '0 auto 1rem',
                  color: '#0a0a0a',
                  fontWeight: 'bold'
                }}>{index + 1}</div>
                <h3 style={{
                  fontSize: '1.5rem',
                  marginBottom: '1rem',
                  color: '#c5fb45'
                }}>{item.title}</h3>
                <p style={{ marginBottom: '1.5rem' }}>{item.desc}</p>
                <button style={{
                  padding: '0.5rem 1rem',
                  backgroundColor: 'transparent',
                  color: '#c5fb45',
                  border: '1px solid #c5fb45',
                  borderRadius: '4px',
                  cursor: 'pointer'
                }}>Learn More</button>
              </div>
            ))}
          </div>
        </section>
  )
}

export default Makeupservice;